.profile {
  display: flex;
  justify-content: space-between;
}

.missions {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  text-align: center;
}

.missions ul {
  display: flex;
  flex-direction: column;
  width: 60%;
}

.missions ul li {
  border: 1px solid #000;
  padding: 10px;
  width: 100%;
}
